import { ChatOpenAI } from "@langchain/openai";

export function initModel(model) {
    // 浏览器上面无法直接读取环境变量值，后面需要把生成数据的function变成后端服务接口，启动一个后端服务来生成数据，这样可以避免在代码或者浏览器中存放LLM key的安全问题
    const key= "your LLM key";
    const chatModel = new ChatOpenAI({apiKey: key, model:model});
    return chatModel;
}
